.widgets{
    position: sticky;
    top: 80px;
    width: 20rem;
    background-color: white;
    border-radius: 10px;
    border: 1px solid lightgray;
    height: fit-content;
    padding-bottom: 10px;
}

.widegts__header {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 5px;
    padding: 10px;
}

.widegts__header > h2 {
    font-size: 16px;
    font-weight: 400;
}

.widgets__aritcle {
    display: flex;
    flex-direction: row;
    padding: 10px;
    cursor: pointer;
}

.widgets__article:hover{
    background-color: whitesmoke;
}

.widgets__articleLeft{
    color: #0177b7;
    margin-left: 5px;
}

.widgets__articleLeft > .MuiSvgIcon-root{
    font-size: 15px;
}

.widgets__articleRight > h4 {
    font-size: 14px;
}

.widgets__articleRight > p {
    font-size: 12px;
    color: gray;
}